import { Timeline,TimelineItem,TimelineSeparator,TimelineConnector,TimelineDot,TimelineContent } from '@material-ui/lab'
import { Icon, Typography } from '@mui/material'
import React, { Children } from 'react'
import ResumeData from '../../utils/ResumeData'
import './TimelineCustom.css'

const TimelineCustom = ({title,icon,children}) =>{
  
    return (
      
      <Timeline className="timeline">
        <TimelineItem className="timelineHead">
          <TimelineSeparator>
            <TimelineDot className="timelineDotHeader">{icon}</TimelineDot>
            <TimelineConnector/>
          </TimelineSeparator>
          <TimelineContent>
            <Typography variant="h6" className="timelineTitle">{title}</Typography>
          </TimelineContent>
        </TimelineItem>
      
        {children}
       
      </Timeline>

      
    )
   
}
export const CustomTimelineSeperator = () =>(
  <TimelineSeparator className="SeperatorCustom">
    <TimelineDot className="timelineDot"></TimelineDot>
    <TimelineConnector/>
  </TimelineSeparator>
    
)



export default TimelineCustom

