import { Grid, Typography } from '@mui/material'
import React from 'react'
import ResumeData from '../../utils/ResumeData'
import './Resume.css'
import { TimelineDot,Timeline,TimelineItem,TimelineSeparator,TimelineConnector,TimelineContent} from '@material-ui/lab';
import { Paper } from '@material-ui/core'
import TimelineCustom,{CustomTimelineSeperator} from './TimelineCustom';
import SchoolIcon from '@mui/icons-material/School';
import WorkIcon from '@mui/icons-material/Work';


const Resume = () => {
    return (
        <>
        {/*About Me */}
        <Grid container className="section padbuttom" >
            <Grid item className="title">
                <Typography variant='h5'>Career Objective</Typography>
                <span></span>
            </Grid>
            <Grid item className="title" xs={12}>
                <Typography className="aboutMe">
                    {ResumeData.profile.aboutme}
                </Typography>
            </Grid>

        </Grid>
        {/*End of About Me */}
        
        
        <Grid container className="sectionTimeline">
            {/*Education */}
            <Grid item sm={12} md={6} >
                <TimelineCustom title="Education" icon={<SchoolIcon/>}>
                    
                        <TimelineItem>
                            <CustomTimelineSeperator/>
                                
                            <TimelineContent className="timelineContent">
                                <Typography className="timelineTtile">{ResumeData.education.title}</Typography>
                                <Typography className="timelineDate">{ResumeData.education.date}</Typography>
                                <Typography className="timelineDescription">{ResumeData.education.description}</Typography>
                                <Typography className="timelineDescription">Major : {ResumeData.education.Major}</Typography>
                                <Typography className="timelineDescription">Minor : {ResumeData.education.Minor}</Typography>
                                <Typography className="timelineDescription">Gpa : {ResumeData.education.Gpa}</Typography>
                            </TimelineContent>
                        </TimelineItem>
                    
                </TimelineCustom>
                
            </Grid>
            {/*End of Education */}

            {/*Experience */}
            <Grid item sm={12} md={6}>
                <TimelineCustom title="Experience" icon={<WorkIcon/>}>
                {ResumeData.experience.map((element)=>
                        <TimelineItem>
                            <CustomTimelineSeperator/>
                                
                            <TimelineContent>
                                <Typography className="timelineTtile">{element.title}</Typography>
                                <Typography className="timelineDate">{element.date}</Typography>
                                <Typography className="timelineDescription">{element.description}</Typography>
                            </TimelineContent>
                        </TimelineItem>
                    )}
                </TimelineCustom>
            </Grid>
            {/*End of Experience */}
        
        </Grid>
        
        {/*Skills */}
        <Grid container  className="section padbuttom">
            <Grid item xs={12} className="title">
                <Typography>Skill</Typography>
                <span></span>
            </Grid>
          
            <Grid item  xs={12}>
                <Grid container spacing={3}  className="section padTop padbuttom">
                {ResumeData.skills.map((skill)=>(
                    <Grid item  xs={12} md={6} sm={6}>
                        <Paper elevation={3} className="skill">
                            <Typography variant="h6" className="skillTitle">
                                {skill.title}
                            </Typography>
                                {skill.description.map((element)=>(
                            <Typography variant="body2" className="skillDescription">
                            <TimelineDot variant={'outlined'} className="timelineDot"/> 
                                {element}
                            </Typography>
                                    
                                ))}

                            </Paper>
                        </Grid>
                        ))}
                    </Grid>
            </Grid>
        </Grid>
        {/*End of Skills */}

        {/*Footer*/}
        <Grid container>
            <Grid item xs={12} className="Footer">
                <Typography> Developed by Worapon </Typography>
            </Grid>
        </Grid>
        {/*End of Footer*/}
        </>
    )
}

export default Resume
