import { Typography } from '@mui/material'
import React from 'react'
import './Footer.css'

const Footer = () => {
    return (
        <div className="footer">
            <div className="footerLeft">
                <Typography className="FooterName">Worapon Boonchoo</Typography>
            </div>
            <div className="footerRight">
                <Typography className="FooterCredit"></Typography>
                <Typography>Using React and Material UI</Typography>
            </div>
        </div>
    )
}

export default Footer