import { Typography } from '@mui/material'
import React from 'react'
import './Profile.css'
import Stack from '@mui/material/Stack';
import ResumeData from '../utils/ResumeData';
import TimelineCustom from '../Pages/Resume/TimelineCustom';
import {Button} from '@material-ui/core'
import GitHubIcon from '@mui/icons-material/GitHub';

const Profile = () => {
    return (
        <div className="ProFile containerShadow">
            <div className="ProfileName">
                <Typography className="Name">{ResumeData.profile.myname} <span>{ResumeData.profile.surname}</span></Typography>
                <Typography className="NickName">{ResumeData.profile.nickname}</Typography>
            </div>
            <figure className="ProfileImage ">
                <img src={require('../assets/images/Profile.png').default}  alt="myPortfolio"/>
            </figure>
            <div className="ProfileDetail">
                 {/*Personal Profile*/}
                
                <div className="Title">
                <Typography>Personal profile</Typography>
                <span></span>
                </div>
                <div className="ProfileDescription">
                    <Typography><span>Name </span>: {ResumeData.profile.myname}</Typography>
                    <Typography><span>SurName </span>: {ResumeData.profile.surname}</Typography>
                    <Typography><span>Gender </span>: {ResumeData.profile.gender}</Typography>
                    <Typography><span>Age </span>: {ResumeData.profile.age}</Typography>
                </div>
                {/*End of Personal Profile*/}

                {/*contact*/}
                <div className="Title">
                    <Typography>Contact Information</Typography>
                    
                    <span></span>
                </div>
                <div className="ProfileDescription">
                    <Typography><span>Phone </span>: {ResumeData.contact.phone}</Typography>
                    <Typography><span>Email </span>: {ResumeData.contact.email}</Typography>
                    <Typography><span>Address </span>: {ResumeData.contact.address}</Typography>
                </div>
                {/*end of contact*/}

                 <div className="Title">
                    <Typography>Language Skill</Typography>
                    <span></span>
                </div>
                <div className="ProfileDescription">
                    <Typography><span>Thai </span>: Native</Typography>
                    <Typography><span>English</span>: Intermediate</Typography>
                </div>
                 {/*end of contact*/}
                 

                <div className="ButtonContainer">
                    <Button className="CustomBtn" href="https://github.com/Dearrr" target="_blank" endIcon={<GitHubIcon/>}><span>Visit My GitHub</span></Button>
                </div>
            </div>
        </div>
    )
}

export default Profile
